@use 'base/var' as v;
@use 'base/mixins' as m;

.lineup{
    background-color: v.$pink;
    padding: 5rem 0;
    h3,p{
        text-align: center;
        color: v.$white;
    }
    .day{
        text-transform: uppercase;
        margin: 5rem 0;
    }
}
.escenarios-container{
    //Le daremos un mq cuando queramos un diseño en una pantalla grande
    @include m.tablet {
        @include m.grid(2, 5rem);
    }

    p{
        color: v.$black;
    }
}
.escenario{
    padding-top: 2rem;
    margin: 1rem 0;
    @include m.tablet {
        margin:0;
    }
    //Colocar un icono en un elemento html
    .name-escenario{
        &:after{
            content: '';
            display: block;
            width: 20rem;
            height: 10rem;
            background-size: 15rem;
            background-repeat: no-repeat;
            background-position: center center;
            //alinear el recuadro donde ira el icono
            margin: 2rem auto 0 auto;
        }
    }
}
.edm .name-escenario:after{
    background-image: url(../img/icono_edm.png);
}
.rock .name-escenario:after{
    background-image: url(../img/icono_rock.png);
}
.calendar{
    @include m.resetList;
    li{
        padding: 2.5rem;
        font-weight: 700;
        span{
            font-weight: 400;
        }
    }
}

.bg-green{
    background-color: v.$green;
    //Selecionamos los elementos de la lista impares
    li:nth-child(odd){
        //darken es una funcion de sass
        background-color: darken(v.$green, 10%);
    }
}
.bg-yellow{
    background-color: v.$yellow;
    li:nth-child(odd){
        //darken es una funcion de sass
        background-color: darken(v.$yellow, 15%);
    }
}
