@use 'base/var' as v;
@use 'base/mixins' as m;


.header{
    background-color: v.$green;
    @include m.tablet {
        &.fijo{
            position: fixed;
            top: 0;
            left: 0;
            width: 100%;
            box-shadow: .1rem .2rem .1rem #333;
        }
    }
    h1{
        color: v.$white;
        text-align: center;
        @include m.tablet {
            margin: 0;
        }
    }
    .content-header{
        @include m.tablet {
            display:flex;
            justify-content:space-between;
            align-items: center;
            padding: 2rem;
        }
    }
}
.principal-nav{
    @include m.tablet {
        display: flex;
        gap: 2rem;
    }
    a{
        display: block;
        text-align: center;
        padding-bottom: 2rem;
        color: v.$white;
        font-size: 2.4rem;
        @include m.tablet {
            padding-bottom: 0;
        }

        //el & elimina el especia por default que genera el compilador scss
        &:hover{
            color: v.$yellow;
        }
    }
}