@use 'base/var' as v;
@use 'base/mixins' as m;

//poder ensimar elementos a otros objetos
.video{
    position: relative;
    max-height: 70rem;
    min-height: 50rem;
    //ocultar el video sobrante
    overflow: hidden;
    .overlay{
        position: absolute;
        background: rgb(126,2,195);
        background: linear-gradient(270deg, rgba(126,2,195,0.5) 0%, rgba(255,0,110,0.3) 100%);

        height: 100%;
        width: 100%;

        //alinear verticalmente
        display: flex;
        flex-direction: column;
        justify-content: center;
    }

    video{
        @include m.desktop {
            width: 100%;
        }
        width: auto;
        display: block;
    }
}
.content-video{
    z-index: 10;
    h2{
        color: v.$white;
        font-size: 7rem;
        margin: 0;
    }
    p{
        background-color: v.$pink;
        display: inline-block;
        padding: 1rem 2rem;
        color: v.$white;
        font-weight: 700;
    }
}