@use 'base/var' as v;
@use 'base/mixins' as m;

.galeria{
    h3{
        text-align: center;
    }
    padding: 5rem 0;
}
.galeria-imagenes{
    //Las visualizaciones de telefono o muy pequeñas
    //se colocaran en las propiedades principales
    @include m.grid(2, 2rem);
    @include m.resetList;
    
    @include m.phone {
        @include m.grid(3,2rem);
    }
    @include m.tablet{
        @include m.grid(4,2rem);
    }
    @include m.desktop{
        @include m.grid(6,2rem);
    }
}

.overlay{
    //con fixed le damos una posicion fija
    position: fixed;
    //indicamos que se posicione en la esquina
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;

    //como inicia desde la coordenada 0 de la pantalla
    //en todos los sentidos, le decimos que abarque toda
    //la pantalla
    width: 100%;
    height: 100%;

    //Usamos estas 2 propiedades para centrarla en medio
    //de la pantalla
    display: grid;
    place-items: center;

    background-color: rgb(0 0 0 / 0.5);

    picture{
        //El display block es para que la imagen
        //se adapte al contenedor del opverlay
        display: block;
        max-width: 80rem;
    }
}

.btn-close{
    width: 5rem;
    height: 5rem;
    border-radius: 50%;
    background-color: v.$white;
    color: v.$black;

    display: flex;
    align-items: center;
    justify-content: center;

    font-weight: bold;

    &:hover{
        cursor: pointer;
    }
}