@use 'base/var' as v;
@use 'base/mixins' as m;

.boletos{
    padding: 5rem 0;
    h3{
        text-align: center;
        color: v.$white;
        margin-bottom: 3rem;
    }

    .boletos-container{
        @include m.tablet {
            @include m.grid(2,5rem)
        }
    }
}
