.pase{

    .name-pase{
        color: v.$white;
        text-align: center;
        font-size: 2.6rem;
    }
    &-header,
    &-footer{
        padding: .5rem;
        p{
            text-align: center;
            color: v.$white;
            margin: 0;
        }
    }
    &-header{
        background-color: v.$pink;
        color: v.$white;
        p{
            text-transform: uppercase;
            font-size: 1.6rem;
        }
    }
    &-body{
        background-color: v.$white;
        ul{
            @include m.resetList;
            li,
            :nth-child(odd){
                background-color: v.$white;
                padding: 1rem;
                border-bottom: 1px solid #e1e1e1;
            }
        }
    }
    &-footer{
        background-color: v.$purple;
        font-size: 2.4rem;
    }
}
